@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap);
* {
  margin: 0;
  padding: 0;
}

.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    top: 0;
}

.navbar h6 {
    margin: auto;
    font-size: 30px;
    color: rgb(24, 133, 14);
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.menu-bars:focus {
    outline: none;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text-icons {
    margin: 10px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    transition: 150ms;
}

.nav-text a:hover {
    background-color: #1a83ff;
    font-size: 20px;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}
#logo-container {
    width: 100%;
    height: 20%;
    margin-top: 75px;
    z-index: -1;
}

#logo {
    display: block;
    max-width: 150px;
    height: auto;
    margin: auto;
}

#about {
    display: block;
    height: auto;
    max-width: 60%;
    margin: 75px auto;
    z-index: -1;
}

#about-text {
    display: block;
    font-size: 24px;
    line-height: 1.4;
    font-family: "Cormorant Garamond", serif;
    text-align: center;
    justify-content: center;
    color: rgb(59, 59, 59);
}
